
import Vue from "vue";
import { mapGetters } from "vuex";

export default Vue.extend({
  name: "summaryTable",
  data: () => ({
    tableHeaders: [
      {
        text: "Name",
        value: "product_name",
      },
      {
        text: "Quantity",
        value: "quantity",
        align: "center",
        sortable: false
      },
      {
        text: "Price",
        value: "price",
        sortable: false
      },
      {
        text: "Image",
        value: "image",
        sortable: false
      },
    ],
  }),
  methods: {
    getTotalAmountToBePaid() {
      const sum = this.getItemsInCart.reduce(
        (
          accumulator: any,
          object: {
            price: string;
          }
        ) => {
          return accumulator + parseInt(object.price.slice(1));
        },
        0
      );
      return sum;
    },
    getQuantityOfItem(itemID: string) {
      return this.getItemsInCart.filter(
        (el: { id: string }) => el.id === itemID
      ).length;
    },
    getUniqueItems() {
      return [...new Set(this.getItemsInCart)];
    },
  },
  computed: {
    ...mapGetters(["getItemsInCart"]),
  },
});
